<template>
  <div style="height:100%">
    <div style="padding: 8px 10px 0 10px;">
      <SelectInput
        clearable
        placeholder="搜索···"
        v-model="inputContent"
        @on-enter="doSearch"
        @input="smartSuggest"
      >
        <Select v-model="platform" @on-change="platformChanged" slot="prepend" style="width: 80px">
          <Option value="qq">QQ</Option>
          <Option value="wangyi">网易云</Option>
          <Option value="kugou">酷狗</Option>
        </Select>
      </SelectInput>
    </div>
    <Tabs v-model="contType" class="tabs" @on-click="changeType">
      <TabPane label="音乐" name="songs" class="tab-content">
        <SongList ref="Songs" @playmusic="playSong" @getnext="getNext" />
      </TabPane>
      <TabPane label="MV" name="mv" class="tab-content">
        <MVList ref="Mvs" @playmv="playVideo" @getnext="getNext" />
      </TabPane>
      <TabPane label="专辑" name="album" class="tab-content">敬请期待</TabPane>
      <TabPane label="歌单" name="name3" class="tab-content">敬请期待</TabPane>
    </Tabs>
  </div>
</template>

<script>
// @ is an alias to /src
import SongList from "@/components/SongList.vue";
import MVList from "@/components/MVList.vue";
import { search } from "@/api/apihub.js";
import { Input } from "iview";

export default {
  name: "home",
  components: {
    SelectInput:Input,
    SongList,
    MVList,
  },
  data() {
    return {
      inputContent: "",
      LOCAL_STORAGE_KEY: "SearchKey",
      lastSearch: "",
      platform: "wangyi",
      lastPlatform: "wangyi",
      contType: "songs",
      searched: ["songs"]
    };
  },
  methods: {
    platformChanged() {
      this.doSearch();
    },
    changeType(type) {
      if (this.searched.indexOf(type) == -1) {
        this.doSearch();
        this.searched.push(type);
      }
    },
    doSearch() {
      var key = this.inputContent;
      if (JSON.stringify(key) != '""') {
        
        search(this.platform, this.contType, key, 0).then(response => {
          this.setList(this.contType, response);

          this.lastSearch = key;
          this.lastPlatform = this.platform;

          this.storeCache();
        });
      }
    },
    storeCache() {
      localStorage.setItem(
        this.LOCAL_STORAGE_KEY,
        JSON.stringify({
          key: this.lastSearch,
          platform: this.platform
        })
      );
    },
    smartSuggest() {
      console.log(this.inputContent);
    },
    playSong(song) {
      this.$emit("playmusic", song);
    },
    playVideo(mv) {
      this.$emit("playvideo", mv);
    },
    getNext(page, type) {
      search(this.platform, type, this.lastSearch, page).then(response => {
        this.addList(type, response);
        this.storeCache();
      });
    },
    addList(type, list) {
      switch (type) {
        case "songs":
          this.$refs.Songs.add(list.songs);
          break;
        case "mv":
          this.$refs.Mvs.add(list.videos);
          break;
      }
    },
    setList(type, list) {
      switch (type) {
        case "songs":
          this.$refs.Songs.set(list.songs);
          break;
        case "mv":
          this.$refs.Mvs.set(list.videos);
          break;
      }
    }
  },
  created() {
    // get last search key
    var cache = JSON.parse(localStorage.getItem(this.LOCAL_STORAGE_KEY));
    if (cache) {
      this.lastSearch = cache.key;
      this.inputContent = cache.key;
      this.platform = cache.platform;
      this.doSearch();
    }
  }
};
</script>
<style scoped>
.tab-content {
  max-height: -webkit-fill-available;
  overflow-y: auto;
}

.tabs {
  position: absolute;
  bottom: 0;
  top: 36px;
  width: 100%;
}
</style>

<style>
.ivu-tabs-bar {
  padding: 0 20px;
  margin-bottom: 0;
}
.ivu-tabs-content {
  height: calc(100% - 36px);
}
</style>
