<template>
  <Scroll height :on-reach-bottom="handleReachBottom">
    <transition-group appear tag="ul">
      <div v-for="mv in mvlist" :key="mv.mvid" class="list-item">
        <router-link style="color:black" :to="{ name: 'mvPlayer', params: { mv }}">
          <div class="left-block">
            <div class="no-wrap-text">
              <b>{{mv.name}}</b>
            </div>
            <div>{{mv.artist}}</div>
            <div style="color:gray">{{ toTime(mv.duration) }}</div>
          </div>
          <div style="width: 40%; display: inline-block;">
            <img :src="mv.pic_url" style="border-radius: 5px; " width="100%" />
          </div>
        </router-link>
      </div>
    </transition-group>
  </Scroll>
</template>

<script>
export default {
  name: "MVList",
  components: {},
  data() {
    return {
      mvlist: [],
      EXCEED: false,
      page: 1
    };
  },
  computed: {
    toTime: function() {
      return seconds => {
        var date = new Date(null);
        date.setSeconds(seconds); // specify value for SECONDS here
        return date.toISOString().substr(11, 8);
      };
    }
  },
  methods: {
    playMV(i) {
      this.$emit("playmv", this.mvlist[i]);
    },
    add(list) {
      this.mvlist = this.mvlist.concat(list);
      if (list.length === 20) this.page += 1;
      else this.EXCEED = true;
    },
    set(list) {
      this.mvlist = list;
      this.page = 1;
      this.EXCEED = list.length !== 20;
    },
    handleReachBottom() {
      return new Promise(resolve => {
        if (this.EXCEED) this.$Message.info("没有更多视频了");
        else this.$emit("getnext", this.page, "mv");
        resolve()
      });
    }
  },
  created() {}
};
</script>

<style>
.ivu-scroll-container {
  height: 100%;
}

.ivu-scroll-wrapper {
  height: 100%;
}
</style>
<style scoped lang="scss">
.v-enter {
  opacity: 0;
  transform: translateY(80px);
}

.v-enter-active {
  transition: all 0.6s ease;
}

.no-wrap-text {
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.left-block {
  width: 50%;
  margin: 3%;
  text-align: left;
  display: inline-block;
  vertical-align: top;
}

.list-item:active {
  background-color: rgb(179, 226, 245);
  cursor: pointer;
}
</style>
